/* eslint-disable react/prop-types */
import React from 'react';
import { dripFormGroup } from '../../../src/';
import ErrorText from './ErrorText';

export const FieldGroup = ({
  children,
  hiddenError = false,
  errorStyle,
  meta,
  ...props
}) => (
  <div {...props}>
    {children}

    <ErrorText
      {...meta}
      style={errorStyle}
      hiddenError={hiddenError}
    />
  </div>
);

export default dripFormGroup()(FieldGroup);



