/* eslint-disable react/prop-types */
import React from 'react';
import { RadioButton as MuiRadioButton } from 'material-ui/RadioButton';
import { dripFormField } from '../../../src/';
import ErrorText from './ErrorText';

export const RadioButton = ({
  input: {
    onChange,
    ...input
  },
  props: {
    errorStyle,
    hiddenError = false,
    ...props
  },
  meta: {
    label,
    ...meta
  },
}) => (
  <span>
    <MuiRadioButton
      {...props}
      {...input}
      onCheck={onChange}
      label={label}
    />
    <ErrorText
      {...meta}
      style={errorStyle}
      hiddenError={hiddenError}
    />
  </span>
);

export default dripFormField('radio')(RadioButton);



