/* eslint-disable react/prop-types */
import React from 'react';
import MuiToggle from 'material-ui/Toggle';
import { dripFormField } from '../../../src/';
import ErrorText from './ErrorText';

export const Toggle = ({
  input: {
    onChange,
    checked,
    ...input
  },
  props: {
    hiddenError = false,
    errorStyle,
    ...props
  },
  meta: {
    label,
    ...meta
  },
}) => (
  <span>
    <MuiToggle
      {...props}
      {...input}
      defaultChecked={checked}
      onToggle={onChange}
      label={label}
    />
    <ErrorText
      {...meta}
      style={errorStyle}
      hiddenError={hiddenError}
    />
  </span>
);

export default dripFormField('checkbox')(Toggle);



