/* eslint-disable react/prop-types */
import React from 'react';
import PropTypes from 'prop-types'; // eslint-disable-line

const ErrorText = (props, context) => {
  const {
    muiTheme: {
      textField,
      prepareStyles,
    },
  } = context;

  const {
    error,
    dirty,
    touched,
    hiddenError,
    style,
  } = props;

  const shouldDisplayError = !hiddenError && !!(error && dirty && touched);

  if (!shouldDisplayError) {
    return null;
  }

  return (
    <div
      style={prepareStyles({
        position: 'relative',
        marginTop: 5,
        fontSize: 12,
        lineHeight: '12px',
        color: textField.errorColor,
        ...(style || {}),
      })}
    >
      {error}
    </div>
  );
};

ErrorText.contextTypes = {
  muiTheme: PropTypes.object.isRequired,
};

export default ErrorText;



