/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import MuiSelectField from 'material-ui/SelectField';
import { dripFormField } from '../../../src/';

export class SelectField extends Component {
  handleChange = (e, key, payload) => {
    this.props.input.onChange(payload);
  };

  render() {
    const {
      input: {
        onChange, // eslint-disable-line no-unused-vars
        ...input
      },
      props: {
        children,
        hiddenError = false,
        ...props
      },
      meta: {
        error,
        dirty,
        touched,
      },
    } = this.props;

    const shouldDisplayError = !hiddenError && !!(error && dirty && touched);

    return (
      <MuiSelectField
        {...input}
        {...props}
        onChange={this.handleChange}
        errorText={shouldDisplayError ? error : null}
      >
        {children}
      </MuiSelectField>
    );
  }
}

export default dripFormField('select')(SelectField);



